  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  min-height: 100vh;
  width: 100%;
}

nav {
  background-color: #92e0e5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
}
nav .dog-icon {
  width: 50px;
}
nav .links {
  display: flex;
  gap: 20px;
}
nav .links a {
  font-size: 1.25rem;
  color: white;
  text-decoration: none;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 40px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center/cover;
}
header img {
  width: 300px;
  height: 300px;
  border-radius: 150px;
  object-fit: cover;
  border: 4px solid #3f8da8;
}

main {
  background-color: white;
}

.dog-card-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 50px 20px;
}
.dog-card-container .dog-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 350px;
  text-align: center;
  gap: 10px;
  margin: 0 20px;
  font-size: 1.1rem;
}
.dog-card-container img {
  width: 250px;
  height: 250px;
  border-radius: 125px;
  object-fit: cover;
  border: 3px solid #3f8da8;
}

button {
  border: 5px solid #3f8da8;
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  background-color: white;
  color: #3f8da8;
  font-size: 1.25rem;
  width: fit-content;
  padding: 5px 12px;
}

footer {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  font-size: 1.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  gap: 30px;
  background-color: #3f8da8;
  color: white;
}
footer a {
  color: white;
}
footer .socials {
  display: flex;
  justify-content: space-between;
  width: 400px;
}

h1,
h2 {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
